import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ShopProfileSettingsComponent } from './components/shop-profile-settings/shop-profile-settings.component';
import { ShopProfileComponent } from './components/shop-profile/shop-profile.component';
import { ShopSettingsComponent } from './shop-settings.component';

const routes: Routes = [
  {
    path:'',
    component: ShopSettingsComponent,
    children: [
      {
        path:'',
        redirectTo: 'shop-profile-settings',
        pathMatch: 'full'
      },
      {
        path:'shop-profile-settings',
        component: ShopProfileSettingsComponent,
      }, 
      {
        path:'shop-profile',
        component: ShopProfileComponent,
      }, 
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ShopSettingsRoutingModule { }
